@import url(https://fonts.google.com/specimen/Nunito+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:'Nunito Sans', sans-serif !important;font-weight:bold;color:#333;border-color:#333;line-height:1.5}\::-webkit-input-placeholder{color:rgba(214,63,116,0.5)}\:-moz-placeholder{color:rgba(214,63,116,0.5)}\::-moz-placeholder{color:rgba(214,63,116,0.5)}\:-ms-input-placeholder{color:rgba(214,63,116,0.5)}body{background:#fefefe}.textCenter{text-align:center}.removeBtn{position:absolute;right:8px;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}#container{margin:auto;text-align:center;max-width:700px;margin:60px auto 0 auto;padding:0px 20px}#container h2{margin-bottom:30px;color:#333}#container #form{margin-bottom:50px}#container #info{padding:7px;text-align:left;color:rgba(51,51,51,0.5)}#container input{background:transparent;border:solid 1px;padding:7px 10px;border-right:none;width:70%;color:#333;border-color:#d63f74;outline:none}#container button{border:solid 1px #d63f74;padding:7px 10px;background:rgba(214,63,116,0.8);cursor:pointer;width:15%;min-width:20px;-webkit-transition:all 100ms linear 80ms;transition:all 100ms linear 80ms;color:#fefefe;line-height:1;font-size:18px;outline:none}#container button,#container input{vertical-align:middle;height:35px;box-sizing:border-box}#container input[type='checkbox']{vertical-align:middle;height:14px;width:inherit;box-sizing:border-box;position:absolute;top:39%;right:76px}#container #acu{text-align:left;padding:7px}#container #footer{margin:50px 0 30px 0;padding-top:30px;text-align:left;border-top:dotted 2px #d63f74}#container #footer p{padding:7px;display:inline-block;color:#333;-webkit-transition:all 100ms linear 80ms;transition:all 100ms linear 80ms}#container #footer #footer p:hover{color:#d63f74}#container #footer #footer p:before,#container #footer p:after{color:#6d639a}#container #footer #footer p:before{content:'<'}#container #footer #footer p:after{content:'/>'}#container .todos{-webkit-transition:all 100ms linear 100ms;transition:all 100ms linear 100ms;position:relative;text-align:left;margin:30px auto;padding:7px;padding-right:28px;border:solid 1px transparent;color:#6d639a;display:flex;justify-content:space-between}#container .todos p{word-break:break-word;width:60%}#container .todos:hover{cursor:pointer;border:solid 1px #d63f74}#container span{visibility:visible;background-color:d63f74;color:#d63f74}#container span{-webkit-transition:all 100ms linear 100ms;transition:all 100ms linear 100ms}#container span:hover{cursor:pointer}

